import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Company } from 'src/app/models/company.model';
import { AdminService } from 'src/app/services/admin.service';
import { MatRipple } from '@angular/material/core';
@Component({
  selector: 'app-add-company',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.css'],
})
export class AddCompanyComponent implements OnInit {
  public company = new Company();

  constructor(
    private adminService: AdminService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {}

  public addCompany() {
    console.log(`company name: ${this.company.name}`);

    this.adminService.addCompany(this.company).subscribe(
      (addedCompany) => {
        this.snackBar.open(
          `You have just added a new company with ID: ${addedCompany.id} Successfully`,
          'i got it',
          {
            verticalPosition: 'top',
          }
        );
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
