import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginItem } from 'src/app/models/login-item.model';
import { LogoutItem } from 'src/app/models/logout-item.model';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css'],
})
export class LogoutComponent implements OnInit {
  // private logoutItem = new LogoutItem();

  constructor(private router: Router, private loginService: LoginService) {}

  public logout() {
    console.log(`
    user token: ${localStorage.getItem('token')}
    
    
    `);

    this.loginService.logout().subscribe(
      (logoutItem) => {
        //take client to HOME page and clear the data of user
        localStorage.clear();
        this.router.navigate(['home']);
        alert(`${logoutItem}`);
        // alert(`${logoutItem.answer}`);
      },
      (e) => {
        console.dir(e);
        localStorage.clear();
        this.router.navigate(['home']);
      }
    );
  }
  ngOnInit(): void {}
  public closeModal() {
    document.querySelector('.modal')?.classList.add('hidden');
    document.querySelector('.overlay')?.classList.add('hidden');
  }

  public openModal() {
    document.querySelector('.modal')?.classList.remove('hidden');
    document.querySelector('.overlay')?.classList.remove('hidden');
  }

  public navHome() {
    this.router.navigate(['home']);
  }
}
