import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Company } from 'src/app/models/company.model';
import { AdminService } from 'src/app/services/admin.service';

@Component({
  selector: 'app-get-all-companies',
  templateUrl: './get-all-companies.component.html',
  styleUrls: ['./get-all-companies.component.css'],
})
export class GetAllCompaniesComponent implements OnInit {
  public companies!: Company[];

  constructor(
    private adminService: AdminService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    // this.products = this.ProductsService.getAllProducts
    this.adminService.getAllCompanies().subscribe(
      (arrOfCompanies) => {
        if (arrOfCompanies.length == 0) {
          this.snackBar.open(`No companies to show :(`, 'i got it', {
            verticalPosition: 'top',
            duration: 4000,
          });
        }
        this.companies = arrOfCompanies;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
