import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

import { AdminService } from 'src/app/services/admin.service';

@Component({
  selector: 'app-delete-company',
  templateUrl: './delete-company.component.html',
  styleUrls: ['./delete-company.component.css'],
})
export class DeleteCompanyComponent implements OnInit {
  public company_id!: number;

  constructor(
    private adminService: AdminService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    console.log(localStorage.getItem('token'));
  }

  public deleteCompany() {
    console.log(`
    company Id: ${this.company_id} `);

    this.adminService.deleteCompany(this.company_id).subscribe(
      (answer) => {
        this.snackBar.open(`${answer.concat('')}`, 'i got it', {
          verticalPosition: 'top',
        });
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
