import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
})
export class HeaderComponent implements OnInit {
  public navClicked!: boolean;

  //======================================================================
  //======================================================================
  public isLoggedIn = localStorage.getItem('is_login');
  public theClientType = localStorage.getItem('client_type');

  constructor(private router: Router) {}

  ngOnInit(): void {
    this.navClicked = false;

    console.log(localStorage.getItem('is_login'));
  }

  public userSpaceNavigate() {
    let type = localStorage.getItem('client_type');
    console.log(type);

    switch (type) {
      case 'Administrator':
        this.router.navigate(['admin/admin-layout']);
        break;
      case 'Company':
        this.router.navigate(['company/company-menu']);
        break;
      case 'Customer':
        this.router.navigate(['customer/customer-menu']);
        break;

      default:
        this.router.navigate(['unautorized']);
        break;
    }
  }

  public onLinksLiClick() {
    if (this.navClicked) {
      this.navSlide();
    }
  }
  public navSlide() {
    document.querySelector('.nav-links')?.classList.toggle('nav-active');
    document.querySelector('.burger')?.classList.toggle('toggle');
    if (this.navClicked) {
      this.navClicked = false;
    } else {
      this.navClicked = true;
    }
  }
}
