import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Company } from '../models/company.model';
import { Customer } from '../models/customer.model';

@Injectable({
  providedIn: 'root',
})
export class AdminService {

  public BASE_URL ='';


  constructor(private httpClient: HttpClient) {}

  public addCompany(company: Company): Observable<Company> {
    //===========================================================
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );

    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.post<Company>(
      `${this.BASE_URL}/api/admin/add/company`,
      company,
      options
    );
  }

  public updateCompany(
    companyId: Number,
    company: Company
  ): Observable<string> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.put(
      `${this.BASE_URL}/api/admin/update/company?companyId=` + companyId,
      company,
      options
    );
  }
  public getOneCompany(companyId: Number): Observable<Company> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Company>(
      `${this.BASE_URL}/api/admin/get/company?companyId=` + companyId,
      options
    );
  }

  public deleteCompany(companyId: Number): Observable<string> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.delete(
      `${this.BASE_URL}/api/admin/delete/company?companyId=` + companyId,
      options
    );
  }
  public getAllCompanies(): Observable<Company[]> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Company[]>(
      `${this.BASE_URL}/api/admin/getAll/companies`,
      options
    );
  }

  //===============================================================
  //================Customers Methods================
  //===============================================================
  public addCustomer(customer: Customer): Observable<Customer> {
    //===========================================================
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );

    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.post<Customer>(
      `${this.BASE_URL}/api/admin/add/customer`,
      customer,
      options
    );
  }

  public updateCustomer(
    customerId: Number,
    customer: Customer
  ): Observable<string> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.put(
      `${this.BASE_URL}/api/admin/update/customer?customerId=` +
        customerId,
      customer,
      options
    );
  }

  public deleteCustomer(customerId: Number): Observable<string> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.delete(
      `${this.BASE_URL}/api/admin/delete/customer?customerId=` +
        customerId,
      options
    );
  }
  public getOneCustomer(customerId: Number): Observable<Customer> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Customer>(
      `${this.BASE_URL}/api/admin/get/customer?customerId=` + customerId,
      options
    );
  }
  public getAllCustomers(): Observable<Customer[]> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Customer[]>(
      `${this.BASE_URL}/api/admin/getAll/customers`,
      options
    );
  }
}
