import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Coupon } from 'src/app/models/coupon.model';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-get-coupons-by-limit-price',
  templateUrl: './get-coupons-by-limit-price.component.html',
  styleUrls: ['./get-coupons-by-limit-price.component.css'],
})
export class GetCouponsByLimitPriceComponent implements OnInit {
  public maxPrice!: number;
  public coupons!: Coupon[];

  constructor(
    private companyService: CompanyService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {}
  public getCouponsByLimitPrice() {
    console.log(`
    limit: ${this.maxPrice}
    `);
    this.companyService.getCouponsByMaxPrice(this.maxPrice).subscribe(
      (arrOfCoupons) => {
        if (arrOfCoupons.length == 0) {
          this.snackBar.open(`No coupons to show :(`, 'i got it', {
            verticalPosition: 'top',
            duration: 4000,
          });
        }
        this.coupons = arrOfCoupons;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
