import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Category } from '../models/category.model';
import { Coupon } from '../models/coupon.model';
import { Customer } from '../models/customer.model';

@Injectable({
  providedIn: 'root',
})
export class CustomerService {
  public BASE_URL = '';

  constructor(private httpClient: HttpClient) {}

  public purchaseCoupon(couponId: number): Observable<string> {
    //===========================================================
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.post(
       `${this.BASE_URL}/api/customer/purchase?couponId=` + couponId,
      null,
      options
    );
  }

  public getAllCoupons(): Observable<Coupon[]> {
    //===========================================================
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Coupon[]>(
      `${this.BASE_URL}/api/customer/get/all`,
      options
    );
  }

  public getCouponsByCategory(category: Category): Observable<Coupon[]> {
    //===========================================================
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Coupon[]>(
      `${this.BASE_URL}/api/customer/get/byCategory?category=` + category,
      options
    );
  }

  public getCouponsByMaxPrice(maxPrice: Number): Observable<Coupon[]> {
    //===========================================================
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Coupon[]>(
      `${this.BASE_URL}/api/customer/get/byMaxPrice?maxPrice=` + maxPrice,
      options
    );
  }

  public getDetails(): Observable<Customer> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Customer>(
      `${this.BASE_URL}/api/customer/getDetails`,
      options
    );
  }

  public getWebsiteCoupons(): Observable<Coupon[]> {
    //===========================================================
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Coupon[]>(
      `${this.BASE_URL}/api/customer/get/websiteCoupons`,
      options
    );
  }
}
