import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-delete-coupon',
  templateUrl: './delete-coupon.component.html',
  styleUrls: ['./delete-coupon.component.css'],
})
export class DeleteCouponComponent implements OnInit {
  public coupon_id!: number;

  constructor(
    private companyService: CompanyService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {}

  public deleteCoupon() {
    console.log(`
    customer ID: ${this.coupon_id}
    
    `);

    this.companyService.deleteCoupon(this.coupon_id).subscribe(
      (answer) => {
        this.snackBar.open(`${answer.concat('')} `, 'i got it', {
          verticalPosition: 'top',
        });
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
