import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-unautorized-client',
  templateUrl: './unautorized-client.component.html',
  styleUrls: ['./unautorized-client.component.css'],
})
export class UnautorizedClientComponent implements OnInit {
  constructor(private router: Router) {}

  ngOnInit(): void {
    localStorage.clear();
  }
  public navHome() {
    this.router.navigate(['home']);
  }
  public navLogin() {
    this.router.navigate(['login']);
  }
}
