import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Coupon } from 'src/app/models/coupon.model';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-purchase-coupon',
  templateUrl: './purchase-coupon.component.html',
  styleUrls: ['./purchase-coupon.component.css'],
})
export class PurchaseCouponComponent implements OnInit {
  public coupon_id!: number;
  public client_name!: string;
  public allWebsiteCoupons!: Coupon[];
  constructor(
    private customerService: CustomerService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.client_name = localStorage.getItem('Name')!;
    // show all coupons of the website to the client
    this.customerService.getWebsiteCoupons().subscribe(
      (arrOfCoupons) => {
        if (arrOfCoupons.length == 0) {
          this.snackBar.open(`No coupons to show :(`, 'i got it', {
            verticalPosition: 'top',
            duration: 4000,
          });
        }
        this.allWebsiteCoupons = arrOfCoupons;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }

  public purchaseCoupon(couponId: string) {
    //convert couponId to Number as the HTTP request in the server
    let id_as_number = Number(couponId);

    this.customerService.purchaseCoupon(id_as_number).subscribe(
      (answer) => {
        this.snackBar.open(
          `Thank you ${this.client_name} for being a valued member - ${answer} `,
          'i got it',
          {
            verticalPosition: 'top',
          }
        );
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }

  // public deleteProduct(product_id: string) {
  //   const id_as_number = Number(product_id);
  //   this.productsService.deleteProductHttp(id_as_number);
  // }
}
