import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Category } from 'src/app/models/category.model';
import { Coupon } from 'src/app/models/coupon.model';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-get-coupons-by-category',
  templateUrl: './get-coupons-by-category.component.html',
  styleUrls: ['./get-coupons-by-category.component.css'],
})
export class GetCouponsByCategoryComponent implements OnInit {
  public categories: Category[] = [
    Category.Electricity,
    Category.Food,
    Category.Restaurant,
    Category.Sport,
    Category.Vacation,
  ];
  public category!: Category;
  public coupons!: Coupon[];

  constructor(
    private companyService: CompanyService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {}
  public getCouponsByCategory() {
    console.log(`
    category: ${this.category}
    `);
    this.companyService.getCouponsByCategory(this.category).subscribe(
      (arrOfCoupons) => {
        if (arrOfCoupons.length == 0) {
          this.snackBar.open(`No coupons to show :(`, 'i got it', {
            verticalPosition: 'top',
            duration: 4000,
          });
        }
        this.coupons = arrOfCoupons;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
