import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Company } from 'src/app/models/company.model';
import { Customer } from 'src/app/models/customer.model';
import { CompanyService } from 'src/app/services/company.service';

@Component({
  selector: 'app-get-com-details',
  templateUrl: './get-com-details.component.html',
  styleUrls: ['./get-com-details.component.css'],
})
export class GetComDetailsComponent implements OnInit {
  public company!: Company;

  constructor(
    private companyService: CompanyService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    // this.products = this.ProductsService.getAllProducts
    this.companyService.getDetails().subscribe(
      (companyFromDB) => {
        this.company = companyFromDB;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
