import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AboutComponent } from './components/about/about.component';
import { AdminLayoutComponent } from './components/admin/admin-layout/admin-layout.component';
import { AdminMenuComponent } from './components/admin/admin-menu/admin-menu.component';
import { AdminComponent } from './components/admin/admin.component';
import { AddCompanyComponent } from './components/admin/apis/add-company/add-company.component';
import { AddCustomerComponent } from './components/admin/apis/add-customer/add-customer.component';
import { DeleteCompanyComponent } from './components/admin/apis/delete-company/delete-company.component';
import { DeleteCustomerComponent } from './components/admin/apis/delete-customer/delete-customer.component';
import { GetAllCompaniesComponent } from './components/admin/apis/get-all-companies/get-all-companies.component';
import { GetAllCustomersComponent } from './components/admin/apis/get-all-customers/get-all-customers.component';
import { GetOneCompanyComponent } from './components/admin/apis/get-one-company/get-one-company.component';
import { GetOneCustomerComponent } from './components/admin/apis/get-one-customer/get-one-customer.component';
import { UpdateCompanyComponent } from './components/admin/apis/update-company/update-company.component';
import { UpdateCustomerComponent } from './components/admin/apis/update-customer/update-customer.component';
import { AddCouponComponent } from './components/company/apis/add-coupon/add-coupon.component';
import { DeleteCouponComponent } from './components/company/apis/delete-coupon/delete-coupon.component';
import { GetAllCouponsnComponent } from './components/company/apis/get-all-couponsn/get-all-couponsn.component';
import { GetComDetailsComponent } from './components/company/apis/get-com-details/get-com-details.component';
import { GetCouponsByCategoryComponent } from './components/company/apis/get-coupons-by-category/get-coupons-by-category.component';
import { GetCouponsByLimitPriceComponent } from './components/company/apis/get-coupons-by-limit-price/get-coupons-by-limit-price.component';
import { UpdateCouponComponent } from './components/company/apis/update-coupon/update-coupon.component';
import { CompanyMenuComponent } from './components/company/company-menu/company-menu.component';
import { CompanyComponent } from './components/company/company.component';
import { AllCouponsComponent } from './components/customer/apis/all-coupons/all-coupons.component';
import { CouponsByCategoryComponent } from './components/customer/apis/coupons-by-category/coupons-by-category.component';
import { CouponsByLimitPriceyComponent } from './components/customer/apis/coupons-by-limit-pricey/coupons-by-limit-pricey.component';
import { GetCusDetailsComponent } from './components/customer/apis/get-cus-details/get-cus-details.component';
import { PurchaseCouponComponent } from './components/customer/apis/purchase-coupon/purchase-coupon.component';
import { CustomerMenuComponent } from './components/customer/customer-menu/customer-menu.component';
import { CustomerComponent } from './components/customer/customer.component';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { LogoutComponent } from './components/logout/logout.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { UnautorizedClientComponent } from './components/unautorized-client/unautorized-client.component';
import { MenuGuard } from './guards/menu.guard';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'login', component: LoginComponent },
  { path: 'logout', component: LogoutComponent },
  { path: 'unautorized', component: UnautorizedClientComponent },

  // { path: 'admin-layout/add-company', component: AddCompanyComponent },
  {
    path: 'admin',
    component: CustomerComponent,
    canActivate: [MenuGuard],
    canActivateChild: [MenuGuard],
    children: [
      { path: 'admin-layout', component: AdminLayoutComponent },
      { path: 'add-company', component: AddCompanyComponent },
      { path: 'update-company', component: UpdateCompanyComponent },
      { path: 'delete-company', component: DeleteCompanyComponent },
      { path: 'get-one-company', component: GetOneCompanyComponent },
      { path: 'get-all-companies', component: GetAllCompaniesComponent },
      { path: 'add-customer', component: AddCustomerComponent },
      { path: 'update-customer', component: UpdateCustomerComponent },
      { path: 'delete-customer', component: DeleteCustomerComponent },
      { path: 'get-one-customer', component: GetOneCustomerComponent },
      { path: 'get-all-customers', component: GetAllCustomersComponent },
    ],
  },
  {
    path: 'company',
    component: CustomerComponent,
    canActivate: [MenuGuard],
    canActivateChild: [MenuGuard],
    children: [
      { path: 'company-menu', component: CompanyMenuComponent },
      { path: 'add-coupon', component: AddCouponComponent },
      { path: 'update-coupon', component: UpdateCouponComponent },
      { path: 'delete-coupon', component: DeleteCouponComponent },
      { path: 'get-all-coupons', component: GetAllCouponsnComponent },
      {
        path: 'get-coupons-by-category',
        component: GetCouponsByCategoryComponent,
      },
      {
        path: 'get-coupons-by-limitprice',
        component: GetCouponsByLimitPriceComponent,
      },
      { path: 'get-com-details', component: GetComDetailsComponent },
    ],
  },
  {
    path: 'customer',
    component: CustomerComponent,
    canActivate: [MenuGuard],
    canActivateChild: [MenuGuard],
    children: [
      { path: 'customer-menu', component: CustomerMenuComponent },
      { path: 'purchase-coupon', component: PurchaseCouponComponent },
      { path: 'all-coupons', component: AllCouponsComponent },
      { path: 'coupons-by-category', component: CouponsByCategoryComponent },
      {
        path: 'coupons-by-limit-price',
        component: CouponsByLimitPriceyComponent,
      },
      { path: 'get-cus-details', component: GetCusDetailsComponent },
    ],
  },
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: '**', component: PageNotFoundComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
