import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

import { Customer } from 'src/app/models/customer.model';
import { AdminService } from 'src/app/services/admin.service';

@Component({
  selector: 'app-get-one-customer',
  templateUrl: './get-one-customer.component.html',
  styleUrls: ['./get-one-customer.component.css'],
})
export class GetOneCustomerComponent implements OnInit {
  public customer_id!: number;
  public customer!: Customer;

  constructor(
    private adminService: AdminService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {}
  public getOneCustomer() {
    console.log(`
    ID: ${this.customer_id}
    `);
    this.adminService.getOneCustomer(this.customer_id).subscribe(
      (customerFromDB) => {
        this.customer = customerFromDB;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }

        console.dir(e);
      }
    );
  }
}
