import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ClientType } from '../models/client-type.model';
import { LoginItem } from '../models/login-item.model';
import { LogoutItem } from '../models/logout-item.model';

@Injectable({
  providedIn: 'root',
})
export class LoginService {
  public BASE_URL_ROOT = '';

  constructor(private httpClient: HttpClient) {}
  public login(
    userEmail: string,
    userPass: string,
    clientType: ClientType
  ): Observable<LoginItem> {
    // let theHeaders = new HttpHeaders().set('ontent-Type', 'plain/text');

    // let OPTIONS = { headers: theHeaders };

    return this.httpClient.post<LoginItem>(
      `${this.BASE_URL_ROOT}/api/login?userEmail=` +
        userEmail +
        '&userPass=' +
        userPass +
        '&clientType=' +
        clientType,
      { responseType: 'text' }
    );
  }
  public logout(): Observable<any> {
    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };

    return this.httpClient.get(
      `${this.BASE_URL_ROOT}/api/client/logout`,
      options
    );
  }
}
