import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Company } from 'src/app/models/company.model';
import { AdminService } from 'src/app/services/admin.service';

@Component({
  selector: 'app-get-one-company',
  templateUrl: './get-one-company.component.html',
  styleUrls: ['./get-one-company.component.css'],
})
export class GetOneCompanyComponent implements OnInit {
  public company_id!: number;
  public company!: Company;
  public errorTemplate!: string;
  public isSubmitClicked!: boolean;
  constructor(
    private adminService: AdminService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.isSubmitClicked = false;
  }
  public getOneCompany() {
    console.log(`
    ID: ${this.company_id}
    `);

    this.adminService.getOneCompany(this.company_id).subscribe(
      (companyFromDB) => {
        this.company = companyFromDB;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
