import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Customer } from 'src/app/models/customer.model';
import { CompanyService } from 'src/app/services/company.service';
import { CustomerService } from 'src/app/services/customer.service';

@Component({
  selector: 'app-get-cus-details',
  templateUrl: './get-cus-details.component.html',
  styleUrls: ['./get-cus-details.component.css'],
})
export class GetCusDetailsComponent implements OnInit {
  public customer!: Customer;

  constructor(
    private customerService: CustomerService,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    // this.products = this.ProductsService.getAllProducts
    this.customerService.getDetails().subscribe(
      (customerFromDB) => {
        this.customer = customerFromDB;
      },
      (e) => {
        if (e.status === 401) {
          console.log(401);
          this.router.navigate(['unautorized']);
        } else {
          this.snackBar.open(`${e.error}`, 'i got it', {
            verticalPosition: 'top',
          });
        }
        console.dir(e);
      }
    );
  }
}
