import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Category } from '../models/category.model';
import { Company } from '../models/company.model';
import { Coupon } from '../models/coupon.model';

@Injectable({
  providedIn: 'root',
})
export class CompanyService {
  public BASE_URL = '';
  constructor(private httpClient: HttpClient) {}

  public addCoupon(coupon: Coupon): Observable<string> {
    //===========================================================
    let theToken = localStorage.getItem('token');
    let theHeaders = new HttpHeaders().set('token', theToken!);

    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.post(
      `${this.BASE_URL}/api/company/add`,
      coupon,
      options
    );
  }

  public updateCoupon(coupon: Coupon, couponId: Number): Observable<string> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.put(
      `${this.BASE_URL}/api/company/update/coupon/` + couponId,
      coupon,
      options
    );
  }

  public deleteCoupon(couponId: Number): Observable<string> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders, responseType: 'text' as const };
    //===========================================================
    return this.httpClient.delete(
      `${this.BASE_URL}/api/company/delete?couponId=` + couponId,
      options
    );
  }

  public getAllCoupons(): Observable<Coupon[]> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Coupon[]>(
      `${this.BASE_URL}/api/company/get/all`,
      options
    );
  }
  public getCouponsByCategory(category: Category): Observable<Coupon[]> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Coupon[]>(
      `${this.BASE_URL}/api/company/get/byCategory?category=` + category,
      options
    );
  }
  public getCouponsByMaxPrice(maxPrice: Number): Observable<Coupon[]> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Coupon[]>(
      `${this.BASE_URL}/api/company/get/byMaxPrice?maxPrice=` + maxPrice,
      options
    );
  }

  public getDetails(): Observable<Company> {
    //===========================================================

    let theHeaders = new HttpHeaders().set(
      'token',
      localStorage.getItem('token')!
    );
    let options = { headers: theHeaders };
    //===========================================================
    return this.httpClient.get<Company>(
      `${this.BASE_URL}/api/company/getDetails`,
      options
    );
  }
}
